import { useSelector } from '../../index';
import React from 'react';
import orderDetailsStyle from './order-details.module.css';

const OrderDetails: React.FC = () => {
  const { order } = useSelector(store => store.order);

  if (!order) {
    return <p className={orderDetailsStyle.state}>...</p>
  }

  return ( 
    <>
      <h2 className={orderDetailsStyle.title}>{order?.order?.number}</h2>
      <p className={orderDetailsStyle.state}>{order?.name}</p>
      <p className={orderDetailsStyle.subtitle}> </p>
      <p className={orderDetailsStyle.notification}>   </p>
      <p className={orderDetailsStyle.notification}>    </p>
    </>
  );
};

export default OrderDetails;
